import { PelamarServiceService } from './../../service/pelamar-service.service';
import { Component, OnInit } from '@angular/core';
import { DirektoriPelamarBiodataService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router } from '@angular/router';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { Pelamar_biodata } from 'src/app/sdk/hr/models';
import { FormControl, Validators } from '@angular/forms';

@Component({
  selector: 'app-edit',
  templateUrl: './edit.component.html',
  styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {

    constructor(
        private direktoriPelamarBiodataService: DirektoriPelamarBiodataService,
        private spinner: NgxSpinnerService,
        private router: Router,
        private pesan: PesanService,
        private pelamarServiceService: PelamarServiceService,
        private fb: FormBuilderTypeSafe,
    ) {}

    id_pelamar: string = ''
    pelamarBiodata: Pelamar_biodata = {}
    ngOnInit() {
        this.pelamarServiceService.getIdPelamar().subscribe(data => {
            this.id_pelamar = data
        })
        this.form()
        this.getBiodata()
    }

    getBiodata() {
        this.direktoriPelamarBiodataService
            .getDirektoriPelamarBiodatasId(this.id_pelamar)
            .subscribe(data => {
                this.dataForm.setValue(data.data)
            })
    }

    dataForm: FormGroupTypeSafe<Pelamar_biodata>
    form() {
        this.dataForm = this.fb.group<Pelamar_biodata>({
            id_pelamar: new FormControl('', [Validators.required]),
            id_pelamar_biodata: new FormControl(''),
            jenis_kelamin: new FormControl(''),
            tempat_lahir: new FormControl(''),
            tanggal_lahir: new FormControl(''),
            agama: new FormControl(''),
            status_perkawinan: new FormControl(''),
            golongan_darah: new FormControl(''),
            no_telepon: new FormControl(''),
            alamat_tinggal: new FormControl(''),
            kota_tinggal: new FormControl(''),
            provinsi_tinggal: new FormControl(''),
            no_identitas: new FormControl(''),
            tanggal_berakhir_identitas: new FormControl(''),
            alamat_identitas: new FormControl(''),
            kode_pos_tinggal: new FormControl(''),
            kota_identitas: new FormControl(''),
            bentuk_tubuh: new FormControl(''),
            bentuk_wajah: new FormControl(''),
            berat_badan: new FormControl(''),
            warna_kulit: new FormControl(''),
            initial: new FormControl(''),
            warna_rambut: new FormControl(''),
            tinggi_badan: new FormControl(''),
            prvinsi_identitas: new FormControl(''),
            kode_pos_identitas: new FormControl(''),
            id_user: new FormControl(''),
            created_at: new FormControl(''),
            updated_at: new FormControl(''),
        })
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form tidak valid')
            this.spinner.hide()
            return
        }
        this.pelamarBiodata = this.dataForm.value
        this.direktoriPelamarBiodataService
            .putDirektoriPelamarBiodatasId({ id: this.id_pelamar, body: this.pelamarBiodata })
            .subscribe(
                data => {
                    this.goToBack()
                    this.spinner.hide()
                },
                err => {
                    this.pesan.pesanWarningForm('Gagal di simpan')
                    this.spinner.hide()
                },
                () => {
                    console.log('HTTP request completed.')
                },
            )
    }

    goToBack() {
        this.router.navigate(['/hr/direktori/detail-pelamar', this.id_pelamar, ])
    }

}
