import { PelamarServiceService } from './../../service/pelamar-service.service';
import { Component, OnInit } from '@angular/core';
import { DirektoriPelamarService, DirektoriPegawaiBiodataService, DirektoriPelamarBiodataService } from 'src/app/sdk/hr/services';
import { Router } from '@angular/router';
import { NzMessageService } from 'ng-zorro-antd';
import { Pelamar, Pelamar_biodata } from 'src/app/sdk/hr/models';

@Component({
  selector: 'app-detail',
  templateUrl: './detail.component.html',
  styleUrls: ['./detail.component.scss']
})
export class DetailComponent implements OnInit {

    constructor(
        private direktoriPelamarBiodataService: DirektoriPelamarBiodataService,
        private direktoriPelamarService: DirektoriPelamarService,
        private router: Router,
        private pelamarServiceService: PelamarServiceService,
        private nzMessageService: NzMessageService
    ) {}

    dataPelamar: Pelamar = {}
    biodata: Pelamar_biodata = {}

    id_pelamar_biodata: string = ''
    id_pelamar: string = ''

    ngOnInit() {
        this.pelamarServiceService.getIdPelamar().subscribe(data => {
            this.id_pelamar = data
            console.log('id pelamar paja tu ' + this.id_pelamar)
        })
        this.getPelamar()
    }

    getPelamar() {
        this.direktoriPelamarService.getDirektoriPelamarsId(this.id_pelamar).subscribe(data => {
            this.dataPelamar = data.data
            this.getBiodata(data.data.id_pelamar)
        })
    }

    getBiodata(id_pelamar: string) {
        this.direktoriPelamarBiodataService
            .getDirektoriPelamarBiodatasId(id_pelamar)
            .subscribe(res => {
                this.biodata = res.data
            })
    }

}
