import { Permintaan_pegawai_list } from 'src/app/sdk/hr/models/permintaan-_pegawai-_list';
import { DirektoriPermintaanPegawaiListService } from 'src/app/sdk/hr/services/direktori-permintaan-pegawai-list.service';
import { ActivatedRoute, Router } from '@angular/router';
import { PelamarServiceService } from './service/pelamar-service.service';
import { Component, OnInit } from '@angular/core';
import { DirektoriPelamarService } from 'src/app/sdk/hr/services';
import { Pelamar } from 'src/app/sdk/hr/models';

@Component({
    selector: 'app-detail-pelamar',
    templateUrl: './detail-pelamar.component.html',
    styleUrls: ['./detail-pelamar.component.scss']
})
export class DetailPelamarComponent implements OnInit {

    constructor(
        private direktoriPelamarService: DirektoriPelamarService,
        private direktoriPermintaanPegawaiListService: DirektoriPermintaanPegawaiListService,
        private pelamarServiceService: PelamarServiceService,
        private activatedRoute: ActivatedRoute,
        private router: Router,
    ) { }

    id_pelamar: string = ''
    dataPelamar: Pelamar = {}
    dataPermintaan: Permintaan_pegawai_list = {}

    ngOnInit() {
        this.activatedRoute.paramMap.subscribe(params => {
            if (params.get('id_pelamar')) {
                this.id_pelamar = params.get('id_pelamar')
                this.pelamarServiceService.setIdpelamar(this.id_pelamar)
                console.log('sdasdas' + this.id_pelamar)
            }
        })
        this.getPelamar()
    }

    foto = 'https://os.alipayobjects.com/rmsportal/QBnOOoLaAfKPirc.png'
    getPelamar() {
        this.direktoriPelamarService.getDirektoriPelamarsId(this.id_pelamar).subscribe(
            data => {
                this.dataPelamar = data.data
                this.getPermintaan(data.data.id_permintaan_pegawai)

                if (data.data.foto) {
                    this.foto = "http://erp-document.vm.devetek.com/download?id_document=" + data.data.foto
                }
            }
        )
    }


    getPermintaan(id_permintaan_pegawai: string) {
        this.direktoriPermintaanPegawaiListService.getDirektoriPermintaanPegawaiListsId(id_permintaan_pegawai).subscribe(
            data => {
                this.dataPermintaan = data.data


            }
        )
    }

    UpdateFoto() {
        this.router.navigate(['hr/direktori/employee/detail-pelamar', this.id_pelamar, 'foto'])
    }

}
