import { Component, OnInit } from '@angular/core';
import { FileDocumentService } from 'src/app/sdk/document/services';
import { Document } from 'src/app/sdk/document/models';
import { Router } from '@angular/router';
import { PelamarServiceService } from '../service/pelamar-service.service';
import { NzMessageService } from 'ng-zorro-antd';
import { NgxSpinnerService } from 'ngx-spinner';
import { SingleUploadService } from 'src/app/components/document/single-upload.service';
import { DirektoriPelamarService } from 'src/app/sdk/hr/services';
import { Pelamar } from 'src/app/sdk/hr/models';

@Component({
    selector: 'app-dokumen',
    templateUrl: './dokumen.component.html',
    styleUrls: ['./dokumen.component.scss']
})
export class DokumenComponent implements OnInit {

    constructor(
        private fileDocumentService: FileDocumentService,
        private router: Router,
        private pelamarServiceService: PelamarServiceService,
        private direktoriPelamarService: DirektoriPelamarService,
        private nzMessageService: NzMessageService,
        private spinner: NgxSpinnerService,
        private singleUploadService: SingleUploadService
    ) { }

    toogleUpload = false

    dataDokumen: Document[] = []
    dataPelamar: Pelamar = {}

    id_pelamar: string = ''

    ngOnInit() {
        this.pelamarServiceService.getIdPelamar().subscribe(data => {
            this.id_pelamar = data
            console.log('id pelamar paja tu ' + this.id_pelamar)

            this.getDokumen()
            this.getPalamar()
        })

        this.singleUploadService.getCallback().subscribe(
            data => {

                if (data == 'pelamar_document.finish') {
                    this.toogleUpload = false;
                }
            }
        )

        this.singleUploadService.getId().subscribe(
            data => {
                if (data) {
                    this.getDokumen()
                }
            }
        )
    }

    togleShowUpload() {

        this.toogleUpload = true;
    }

    getPalamar() {
        this.direktoriPelamarService.getDirektoriPelamarsId(this.id_pelamar).subscribe(
            data=> {
                this.dataPelamar = data.data;

                this.singleUploadService.param.next({
                    for_module: "pelamar_document",
                    for_table: "erp_hr_pelamar.pelamar",
                    for_module_id: data.data.id_pelamar
                })
            }
        )
    }
    getDokumen() {
        this.fileDocumentService.getFileDocuments(JSON.stringify({ id_pelamar: this.id_pelamar })).subscribe(
            data => {
                this.dataDokumen = data.data
            }
        )
    }

    delete(id) {
        this.spinner.show()
        this.fileDocumentService
            .deleteFileDocumentsId(id)
            .subscribe(data => {
                this.getDokumen()
                this.spinner.hide()
            })
    }

    batalDelete() {
        this.nzMessageService.info('click cancel')
    }

}
