import { DokumenComponent } from './dokumen/dokumen.component';
import { EditComponent } from './biodata/edit/edit.component';
import { DetailComponent } from './biodata/detail/detail.component';
import { DetailPelamarComponent } from './detail-pelamar.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { FotoComponent } from './foto/foto.component';


const routes: Routes = [
    {
        path: '',
        component: DetailPelamarComponent,
        children: [
            {
                path: '',
                redirectTo: 'biodata/detail'
            },
            {
                path: 'biodata/detail',
                component: DetailComponent
            },
            {
                path: 'biodata/edit',
                component: EditComponent
            },
            {
                path: 'dokumen',
                component: DokumenComponent
            },
            {
                path: 'foto',
                component: FotoComponent
            },
            {
                path: 'keluarga',
                loadChildren: () =>
                    import('./keluarga/keluarga.module').then(mod => mod.KeluargaModule),
            },
            {
                path: 'id-card',
                loadChildren: () =>
                    import('./id-card/id-card.module').then(mod => mod.IdCardModule),
            },
            {
                path: 'pendidikan',
                loadChildren: () =>
                    import('./pendidikan/pendidikan.module').then(mod => mod.PendidikanModule),
            },
            {
                path: 'pengalaman',
                loadChildren: () =>
                    import('./pengalaman/pengalaman.module').then(mod => mod.PengalamanModule),
            },
            {
                path: 'penilaian',
                loadChildren: () =>
                    import('./penilaian/penilaian.module').then(mod => mod.PenilaianModule),
            },
            {
                path: 'keahlian',
                loadChildren: () =>
                    import('./keahlian/keahlian.module').then(mod => mod.KeahlianModule),
            },
        ],
    },
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class DetailPelamarRoutingModule { }
