import { Component, OnInit } from '@angular/core';
import { DirektoriPelamarService } from 'src/app/sdk/hr/services';
import { PelamarServiceService } from '../service/pelamar-service.service';
import { Pelamar } from 'src/app/sdk/hr/models';
import { SingleUploadService } from 'src/app/components/document/single-upload.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { ActivatedRoute, Router } from '@angular/router';
import { FormControl, Validators } from '@angular/forms';

@Component({
    selector: 'app-foto',
    templateUrl: './foto.component.html',
    styleUrls: ['./foto.component.scss']
})
export class FotoComponent implements OnInit {

    constructor(
        private direktoriPelamarService: DirektoriPelamarService,
        private pelamarServiceService: PelamarServiceService,
        private singleUploadService: SingleUploadService,
        private spinner: NgxSpinnerService,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private activatedRoute: ActivatedRoute,
        private router: Router
    ) { }

    id_pelamar: string = ''
    toogleUpload = false

    dataPelamar: Pelamar = {}
    ngOnInit() {
        this.pelamarServiceService.getIdPelamar().subscribe(data => {
            this.id_pelamar = data

        })
        this.getData()
        this.form()

        this.singleUploadService.getCallback().subscribe(
            data => {

                if (data == 'company.finish') {
                    this.toogleUpload = false;
                }
            }
        )

        this.singleUploadService.getId().subscribe(
            data => {
                if (data) {
                    this.dataForm.patchValue({ foto: data })
                    this.simpan()
                }
            }
        )
    }

    // togleShowUpload() {

    //     this.toogleUpload = true;
    // }
    foto = 'https://os.alipayobjects.com/rmsportal/QBnOOoLaAfKPirc.png'
    getData() {
        this.direktoriPelamarService.getDirektoriPelamarsId(this.id_pelamar).subscribe(
            data => {
                this.dataPelamar = data.data;

                this.dataForm.setValue(data.data)

                this.singleUploadService.param.next({
                    for_module: "hr_pelamar",
                    for_table: "erp_hr.pelamar",
                    for_module_id: data.data.id_pelamar
                })

                // if (data.data.foto) {
                //     this.foto = "http://erp-document.vm.devetek.com/download?id_document=" + data.data.foto
                // }

            }
        )
    }

    dataForm: FormGroupTypeSafe<Pelamar>;
    form() {
        this.dataForm = this.fb.group<Pelamar>(
            {
                id_permintaan_pegawai: new FormControl('', [Validators.required]),
                id_perusahaan: new FormControl(''),
                nama_belakang: new FormControl(''),
                nama_depan: new FormControl(''),
                jenis_kelamin: new FormControl(''),
                kewarganegaraan: new FormControl(''),
                id_pelamar: new FormControl(''),
                id_user: new FormControl(''),
                hp: new FormControl(''),
                id_dokumen: new FormControl(''),
                email: new FormControl(''),
                agama: new FormControl(''),
                tanggal_lahir: new FormControl(''),
                tempat_lahir: new FormControl(''),
                hp2: new FormControl(''),
                status_perkawinan: new FormControl(''),
                phone: new FormControl(''),
                created_at: new FormControl(''),
                updated_at: new FormControl(''),
                status: new FormControl(''),
                foto: new FormControl(''),
                Spesialisasi: new FormControl(''),
                majors: new FormControl(''),

            }
        )
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid');
            this.spinner.hide()
            return;
        }

        this.dataPelamar = this.dataForm.value;
        this.direktoriPelamarService.putDirektoriPelamarsId({ id: this.id_pelamar, body: this.dataPelamar }).subscribe(
            data => {
                this.toogleUpload = false
                this.getData()
                this.goToBack()
                this.spinner.hide()
            }, err => {
                this.pesan.pesanWarningForm('Gagal di simpan');
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            }
        )

    }


    goToBack() {
        this.router.navigate(['/hr/direktori/employee/detail-pelamar', this.id_pelamar])
    }

}
